#content {
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#about, #web-dev-tech, #projects {
    display: block;
    width: 80vw;
    max-width: 1000px;
}

#about h2, #projects h2 {
    font-size: 30px;
}

#about p {
    font-size: 20px;
}

#web-dev-tech h3 {
    display: flex;
    gap: 8px;
}

.title-word {
    animation: color-animation 4s linear infinite;
    font-size: 25px;
}

.title-word-1 {
    --color-1: #DF8453;
    --color-2: #3D8DAE;
    --color-3: #E4A9A8;
  }
  
  .title-word-2 {
    --color-1: #DBAD4A;
    --color-2: #ACCFCB;
    --color-3: #17494D;
  }
  
  .title-word-3 {
    --color-1: #ACCFCB;
    --color-2: #E4A9A8;
    --color-3: #ACCFCB;
  }
  
  .title-word-4 {
    --color-1: #3D8DAE;
    --color-2: #DF8453;
    --color-3: #E4A9A8;
  }
  
  
@keyframes color-animation {
0%    {color: var(--color-1)}
32%   {color: var(--color-1)}
33%   {color: var(--color-2)}
65%   {color: var(--color-2)}
66%   {color: var(--color-3)}
99%   {color: var(--color-3)}
100%  {color: var(--color-1)}
}

#icon-slider div {
    width: 120px;
    height: 120px;
    border: 1px solid black;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 5px;
    font-size: 14px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    color: white;
    background-color: rgb(80, 73, 73);
    padding-top: 5px;
    flex-shrink: 0;
}

#slide {
    overflow: hidden;
}

#icon-slider {
    gap: 15px;
    display: flex;
    animation: scroll 20s linear infinite;
    width: calc(135px * 18);
}

@keyframes scroll {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(calc((-135px * 9) - 17px));
    }
}

#icon-slider div img {
    width: 60%;
    height: 60%;
    object-fit: contain;
    margin-bottom: 2px;
}

#about-line {
    width: 600px;
    border-bottom: 1px solid black;
    margin-top: 50px;
}

#pokemon-memory, #to-do-list {
    width: 65vw;
    max-width: 700px;
    object-fit: contain;
    border: 1px solid black;
    opacity: 0.5;
}

.title {
    background-color: rgb(245, 245, 95);
    width: 250px;
    height: 30px;
    font-size: 20px;
    color: black;
    text-align: center;
    padding-top: 5px;
}

.description {
    width: 60vw;
    max-width: 600px;
    border: 1px solid black;
    font-size: 20px;
    padding: 20px;
    background-color: #555;
    color: white;
}

.description p {
    margin: 0;
}

.desc-line {
    width: 90%;
    border: 1px solid rgb(145, 138, 138);
    margin-top: 20px;
}

.description a {
    color: white;
    display: inline-flex;
    gap: 3px;
    margin-top: 5px;
}

.description a:first-of-type {
    margin-left: 70%;
    margin-right: 20px;
}

.description a img {
    width: 20px;
}

.wrapper {
    position: relative;
    bottom: 200px;
    left: 200px;
}

@media screen and (max-width: 800px) {
    .wrapper {
        position: relative;
        bottom: 100px;
        left: 0;
    }
}